html,
body {
  &,
  & > .ember-view {
    height: 100%;
  }
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  li {
    float: left;
  }
}

ul,
section {
  &:global(.clearfix) {
    &::after {
      content: "";
      clear: both;
      display: block;
    }
  }
}

button:global(.btn) {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  &:hover {
    background-color: #e4e3e3;
  }
}

button:global(.btn-primary) {
  color: #fff;
  background-color: #337ab7;
  border-color: #2e6da4;
  &:hover {
    color: #fff;
    background-color: #286090;
    border-color: #204d74;
  }
}

div:global(.text-center) {
  text-align: center;
}